@import url('https://fonts.googleapis.com/css2?family=Jacquard+24&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Bebas Neue", sans-serif;
  background-color: #e6e6e6;
  height: auto;
  width: 100%;
}

header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f1f1f1;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  padding-top: 10px;
}

header h1{
  font-family: "Jacquard 24", system-ui;
  font-size: 3.5em;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  margin: 0 15px;
  display: flex;
}

nav ul li a {
  text-decoration: none;
  cursor: pointer;
  color: black;
  font-size: 1.8em;
  font-weight: bold;
  transition: color 0.3s;
}

nav ul li a.active{
  text-decoration: underline;
  color: rgba(48, 21, 21, 0.733);
}

main {
  padding: 0 20px;
  padding-top: 10px;
  width: 100%;
  height: auto;
  margin: 0 auto;
}

#main-news {
  display: flex;
  flex-direction: row;
  justify-content: center;
  max-width: 1440px;
  margin: 0 auto;
/* gap: 20px; */
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}

.article {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 35px;
  border-bottom: 1px solid;
  border-right: 1px solid;
  gap: 30px;
  max-width: 1440px;
  margin: 0 auto;
}

.article-contents {
  margin-right: 15px;
  margin: 0 auto;
}

.article-contents h3{
  text-transform: uppercase;
  margin-bottom: 5px;
  font-size: 1.8em;
}

.article-contents h2 a {
  text-decoration: none;
  color: black;
}

.article h2 {
  font-size: 1.8em;
  margin-bottom: 25px;
  text-align: center;
  cursor: pointer;
  width: 100%;
}

.article h2 a:hover{
  color: rgba(0, 0, 0, rgba(0, 0, 0, 0.63));
}

.article p {
  font-size: 1.6em;
  line-height: 1.4;
  color: rgba(0, 0, 0, 0.63);
}

.article-img {
  width: 506px;
  height: auto;
  cursor: pointer;
}

.article-contents .read-more{
  width: 200px;
  height: 35px;
  margin: 35px 15px;
  border-radius: 8px;
  border: 1px solid black ;
  cursor: pointer;
  transition: all 0.5s;
  font-size: 1em;
}

.bx-right-arrow-alt{
  margin-left: 0.3em;
  position: relative;
  font-size: 1em;
  transition: all 0.3s;
}

.article-contents .read-more:hover .bx-right-arrow-alt{
  translate: 50%;
}

.additional-content p{
  margin: 0 auto;
}

.additional-content h4{
  font-size: 1.6em;
}

.popular-articles {
  border-bottom: 1px solid;
  max-width: 1440px;
  margin: 0 auto;
}

.popular-article {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

.popular-article .solo-article {
  width: 390px;
  height: 120px;
  cursor: pointer;
}

.popular-article h2 {
  margin-top: 35px;
  font-size: 1.6em;
  margin-bottom: 15px;
  text-align: center;
  width: 100%;
}

.popular-article a {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.popular-article h3{
  text-transform: uppercase;
  margin: 15px;
  font-size: 1.6em;
}

.popular-article h4{
  padding-left: 15px;
  font-size: 1.4em;
  width: 90%;
  margin: 5px;
}

.popular-article p {
  font-size: 1.4em;
  line-height: 1.4;
  margin-bottom: 15px;
  width: 90%;
  color: rgba(0, 0, 0, 0.63);
}

.hardcover-fiction-books {
  padding: 10px;
}

.hardcover-fiction-books p{
  font-size: 1em;
  width: 80%;
  color: rgba(0, 0, 0, 0.63);
}

.hardcover-fiction-books{
  font-size: 1.6em;
}

.hardcover-fiction {
  display: flex;
  padding: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid;
  max-width: 1440px;
  margin: 0 auto;
}

.hardcover-fiction h3 {
  padding-top: 10px;
  font-size: 1.8em;
  font-weight: bold;
}

.hardcover-fiction .book {
  width: 100px;
  height: auto;
}

footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
  height: 50px;
}

.mobile-img{
  display: none;
}

.tablet-img{
  display: none;
}

#buylinks{
  cursor: pointer;
  font-size: 1em;
  width: 140px;
  height: 40px;
  border-radius: 8px;
  border: 1px solid black;
}

.additional-content{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@media screen and (max-width:1160px) {
  .article{
    padding: 15px;
  }

  .article-img {
    width: 300px;
    height: 300px;
    cursor: pointer;
  }

  .popular-article .solo-article{
    width: 300px;
    height: auto;
  }

  .additional-content{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width:920px) {
  main{
    padding: 0 ;
  }

  nav

  .additional-content{
    padding-left: 0;
  }

  .additional-content p{
    font-size: 1.4em;
  }

  .additional-content h4{
    font-size: 1.2em;
  }

  .article-img{
    display: none;
  }
  .article-contents h2 a{
    font-size: 1.6em;
  }

  .article-contents h2{
    margin-bottom: 15px;
    margin-top: 10px;
  }

  .article{
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .article-contents .read-more{
    margin: 15px 10px;
    width: 100px;
    height: 25px;
  }

  .article-contents {
    margin-right: 0;
  }

  .popular-articles{
    padding: 0 15px;
  }

  .popular-article{
    padding: 0;
  }

  .popular-article h2{
    font-size: 1.4em;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .title{
    font-size: 1em;
  }

  .popular-article p{
    font-size: 1em;
    text-align: center;
  }

  .hardcover-fiction{
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .hardcover-fiction .book{
    width: 100px;
    height: 100px;
  }

  .hardcover-fiction-books{
    width: 50%;
  }

  .hardcover-fiction-books h2{
    font-size: 1em;
  }

  .hardcover-fiction-books p{
    font-size: 0.8em;
    margin-bottom: 10px;
  }

  .popular-article .solo-article{
    width: 100%;
    height: 150px;
    padding-top: 15px;
  }

  .article-contents p{
    font-size: 1.4em;
    padding: 10px;
  }
  .tablet-img{
    display: block;
    width: 100%;
    height: 250px;
  }

  .article-contents .read-more{
    width: 100px;
    height: 25px;
    font-size: .7em;
  }

  #buylinks{
    cursor: pointer;
    font-size: .7em;
    width: 100px;
    height: 25px;
  }
}


@media screen and (max-width: 430px) {
  body{
    width: 100%;
    height: auto;
  }

  nav ul li a{
    font-size: 1em;
  }

  header{
    width: 100%;
  }

  header h1{
    font-size: 1.2em;
  }

  main{
    padding: 15px 20px;
  }

  .hardcover-fiction-books h2{
    font-size: 1.6em;
    margin-bottom: 15px;
  }

  .hardcover-fiction-books p{
    font-size: 1.2em;
    margin-bottom: 15px;
    color: rgba(0, 0, 0, 0.63);
  }

  .featured-story{
    border-right: 0 ;
  }

  #main-news {
    display: flex;
    flex-direction: column;
  }

  .article{
    display: flex;
    flex-direction: column;
    padding: 0;
    padding-bottom: 15px;
    justify-content: center;
    align-items: center;
    border-right: 0;
    margin-bottom: 10px;
  }

  .article-contents{
    margin-right: 0;
    margin-top: 15px;
    margin: 10px 15px;
  }

  .hardcover-fiction{
    padding: 10px 0;
    margin: 15px 0;
  }

  .hardcover-fiction h3{
    font-size: 1.6em;
  }

  .hardcover-fiction .book{
    width: 40%;
    height: auto;
  }

  .article-img {
    display: none;
  }

  .popular-article .solo-article{
    width: 100%;
    height: auto;
  }

  .popular-article h2{
    font-size: 1.4em;
  }

  .popular-article h4{
    font-size: 1.2em;
  }

  .popular-article p{
    font-size: 1.6em;
  }

  .mobile-img{
    display: block;
    width: 100%;
    height: auto;
  }

  .tablet-img{
    display: none;
  }

  .additional-content{
    padding-left: 0;
  }

  .article-contents .read-more{
    width: 100px;
    height: 25px;
    font-size: .7em;
  }

  .article-contents h2{
    font-size: 1em
  }

  .article-contents p{
    font-size: 1em;
  }

  .additional-content p{
    font-size: 1em;
    padding: 10px;
  }

  .additional-content h4{
    font-size: .8em;
  }

  #buylinks{
    cursor: pointer;
    font-size: .7em;
    width: 100px;
    height: 25px;
  }
}
