@import "https://fonts.googleapis.com/css2?family=Jacquard+24&display=swap";
@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e6e6e6;
  width: 100%;
  height: auto;
  font-family: Bebas Neue, sans-serif;
}

header {
  background-color: #f1f1f1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: 10px;
  display: flex;
  box-shadow: 0 4px 4px #00000040;
}

header h1 {
  font-family: "Jacquard 24", system-ui;
  font-size: 3.5em;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  margin: 0 15px;
  display: flex;
}

nav ul li a {
  cursor: pointer;
  color: #000;
  font-size: 1.8em;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav ul li a.active {
  color: #301515bb;
  text-decoration: underline;
}

main {
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 10px 20px 0;
}

#main-news {
  flex-direction: row;
  justify-content: center;
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
}

.title {
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  display: flex;
}

.article {
  border-bottom: 1px solid;
  border-right: 1px solid;
  justify-content: center;
  gap: 30px;
  max-width: 1440px;
  margin: 0 auto;
  padding: 35px;
  display: flex;
  position: relative;
}

.article-contents {
  margin: 0 auto;
}

.article-contents h3 {
  text-transform: uppercase;
  margin-bottom: 5px;
  font-size: 1.8em;
}

.article-contents h2 a {
  color: #000;
  text-decoration: none;
}

.article h2 {
  text-align: center;
  cursor: pointer;
  width: 100%;
  margin-bottom: 25px;
  font-size: 1.8em;
}

.article h2 a:hover {
  color: rgba(0, 0, 0, #000000a1);
}

.article p {
  color: #000000a1;
  font-size: 1.6em;
  line-height: 1.4;
}

.article-img {
  cursor: pointer;
  width: 506px;
  height: auto;
}

.article-contents .read-more {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 8px;
  width: 200px;
  height: 35px;
  margin: 35px 15px;
  font-size: 1em;
  transition: all .5s;
}

.bx-right-arrow-alt {
  margin-left: .3em;
  font-size: 1em;
  transition: all .3s;
  position: relative;
}

.article-contents .read-more:hover .bx-right-arrow-alt {
  translate: 50%;
}

.additional-content p {
  margin: 0 auto;
}

.additional-content h4 {
  font-size: 1.6em;
}

.popular-articles {
  border-bottom: 1px solid;
  max-width: 1440px;
  margin: 0 auto;
}

.popular-article {
  flex-direction: column;
  align-items: center;
  padding: 15px;
  display: flex;
}

.popular-article .solo-article {
  cursor: pointer;
  width: 390px;
  height: 120px;
}

.popular-article h2 {
  text-align: center;
  width: 100%;
  margin-top: 35px;
  margin-bottom: 15px;
  font-size: 1.6em;
}

.popular-article a {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.popular-article h3 {
  text-transform: uppercase;
  margin: 15px;
  font-size: 1.6em;
}

.popular-article h4 {
  width: 90%;
  margin: 5px;
  padding-left: 15px;
  font-size: 1.4em;
}

.popular-article p {
  color: #000000a1;
  width: 90%;
  margin-bottom: 15px;
  font-size: 1.4em;
  line-height: 1.4;
}

.hardcover-fiction-books {
  padding: 10px;
}

.hardcover-fiction-books p {
  color: #000000a1;
  width: 80%;
  font-size: 1em;
}

.hardcover-fiction-books {
  font-size: 1.6em;
}

.hardcover-fiction {
  border-bottom: 1px solid;
  max-width: 1440px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.hardcover-fiction h3 {
  padding-top: 10px;
  font-size: 1.8em;
  font-weight: bold;
}

.hardcover-fiction .book {
  width: 100px;
  height: auto;
}

footer {
  background-color: #f1f1f1;
  justify-content: center;
  align-items: center;
  height: 50px;
  display: flex;
}

.mobile-img, .tablet-img {
  display: none;
}

#buylinks {
  cursor: pointer;
  border: 1px solid #000;
  border-radius: 8px;
  width: 140px;
  height: 40px;
  font-size: 1em;
}

.additional-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (width <= 1160px) {
  .article {
    padding: 15px;
  }

  .article-img {
    cursor: pointer;
    width: 300px;
    height: 300px;
  }

  .popular-article .solo-article {
    width: 300px;
    height: auto;
  }

  .additional-content {
    flex-direction: column;
    align-items: center;
    display: flex;
  }
}

@media (width <= 920px) {
  main {
    padding: 0;
  }

  nav .additional-content {
    padding-left: 0;
  }

  .additional-content p {
    font-size: 1.4em;
  }

  .additional-content h4 {
    font-size: 1.2em;
  }

  .article-img {
    display: none;
  }

  .article-contents h2 a {
    font-size: 1.6em;
  }

  .article-contents h2 {
    margin-top: 10px;
    margin-bottom: 15px;
  }

  .article {
    flex-direction: column;
    align-items: center;
    padding: 20px;
    display: flex;
  }

  .article-contents .read-more {
    width: 100px;
    height: 25px;
    margin: 15px 10px;
  }

  .article-contents {
    margin-right: 0;
  }

  .popular-articles {
    padding: 0 15px;
  }

  .popular-article {
    padding: 0;
  }

  .popular-article h2 {
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 1.4em;
  }

  .title {
    font-size: 1em;
  }

  .popular-article p {
    text-align: center;
    font-size: 1em;
  }

  .hardcover-fiction {
    justify-content: center;
    align-items: center;
    padding: 0;
    display: flex;
  }

  .hardcover-fiction .book {
    width: 100px;
    height: 100px;
  }

  .hardcover-fiction-books {
    width: 50%;
  }

  .hardcover-fiction-books h2 {
    font-size: 1em;
  }

  .hardcover-fiction-books p {
    margin-bottom: 10px;
    font-size: .8em;
  }

  .popular-article .solo-article {
    width: 100%;
    height: 150px;
    padding-top: 15px;
  }

  .article-contents p {
    padding: 10px;
    font-size: 1.4em;
  }

  .tablet-img {
    width: 100%;
    height: 250px;
    display: block;
  }

  .article-contents .read-more {
    width: 100px;
    height: 25px;
    font-size: .7em;
  }

  #buylinks {
    cursor: pointer;
    width: 100px;
    height: 25px;
    font-size: .7em;
  }
}

@media screen and (width <= 430px) {
  body {
    width: 100%;
    height: auto;
  }

  nav ul li a {
    font-size: 1em;
  }

  header {
    width: 100%;
  }

  header h1 {
    font-size: 1.2em;
  }

  main {
    padding: 15px 20px;
  }

  .hardcover-fiction-books h2 {
    margin-bottom: 15px;
    font-size: 1.6em;
  }

  .hardcover-fiction-books p {
    color: #000000a1;
    margin-bottom: 15px;
    font-size: 1.2em;
  }

  .featured-story {
    border-right: 0;
  }

  #main-news {
    flex-direction: column;
    display: flex;
  }

  .article {
    border-right: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    padding: 0 0 15px;
    display: flex;
  }

  .article-contents {
    margin: 10px 15px;
  }

  .hardcover-fiction {
    margin: 15px 0;
    padding: 10px 0;
  }

  .hardcover-fiction h3 {
    font-size: 1.6em;
  }

  .hardcover-fiction .book {
    width: 40%;
    height: auto;
  }

  .article-img {
    display: none;
  }

  .popular-article .solo-article {
    width: 100%;
    height: auto;
  }

  .popular-article h2 {
    font-size: 1.4em;
  }

  .popular-article h4 {
    font-size: 1.2em;
  }

  .popular-article p {
    font-size: 1.6em;
  }

  .mobile-img {
    width: 100%;
    height: auto;
    display: block;
  }

  .tablet-img {
    display: none;
  }

  .additional-content {
    padding-left: 0;
  }

  .article-contents .read-more {
    width: 100px;
    height: 25px;
    font-size: .7em;
  }

  .article-contents h2, .article-contents p {
    font-size: 1em;
  }

  .additional-content p {
    padding: 10px;
    font-size: 1em;
  }

  .additional-content h4 {
    font-size: .8em;
  }

  #buylinks {
    cursor: pointer;
    width: 100px;
    height: 25px;
    font-size: .7em;
  }
}
